body {
  background: #f2f2f2;
  font-family: "Open Sans", sans-serif;
}

.search {
  width: 100%;
  position: relative;
  display: flex;
}

.searchTerm {
  width: 100%;
  border: 3px solid #101ca5;
  border-right: none;
  padding: 5px;
  height: 46px;
  border-radius: 5px 0 0 5px;
  outline: none;
  color: #000000;
}
.searchButton {
  width: 50px;
  height: 46px;
  border: 1px solid #100991;
  background: #bec0d3;
  text-align: center;
  color: rgb(4, 30, 134);
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  font-size: 20px ;
}

.wrap {
  width: 30%;
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translate(-50%, -50%);
}
